






















export default {
	data() {
		return {
			show:true,
			dissend:false,
			mobile:'',
			captcha:'',
			isSend:false,
			disabled:false,
			countDownNum:'60'
		}
	},
	onLoad() {
		
	},
	methods:{
		change_o(){
			this.show=false
		},
		change_t(){
			this.show=true
		},
		send_code(){
			var that = this
			if(this.mobile==null || this.mobile==''){
				uni.showToast({
					title:'输入手机号'
				})
				return false;
			}else{
			that.dissend = true;
			that.isSend = true;
			that.disabled = true;
			let countDownNum = that.countDownNum;
			var timer = setInterval(function(){
				countDownNum--;
				// console.log(JSON.stringify(countDownNum));
				that.countDownNum = countDownNum;
				console.log(JSON.stringify(that.countDownNum));
				if(countDownNum ==0){
					clearInterval(timer)
					that.countDownNum = 60;
					that.isSend = false;
					that.disabled = false;
					that.dissend = false;
				}
			},1000)
			uni.request({
				url:'http://api.xinmicredit.cn/user/sendSms/',
				method:'POST',
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				data:{
					 mobile:this.mobile
				},
				success: (res) => {
					console.info(JSON.stringify(res))
				},
				fail: (res) => {
					uni.showToast({
						title:'网络错误'
					})
				}
			})
			}
		},
		denglu(){
			if(this.mobile==''||this.mobile==null){
				uni.showToast({
					title:'输入手机号'
				})
				return false;
			}
			if(this.captcha==''||this.captcha==null){
				uni.showToast({
					title:'输入验证码'
				})
				return false;
			}
			if(this.show){
				uni.showToast({
					title:'同意用户协议'
				})
				return false;
			}			
			uni.request({
				url:'http://api.xinmicredit.cn/user/loginBySms',
				method:'POST',
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				data:{
					 mobile:this.mobile,
					 captcha:this.captcha
				},					
				success: (res) => {
					console.info(JSON.stringify(res.data.code));
					if(res.data.code=="0000"){
						console.info(JSON.stringify(res.data.data))
						uni.setStorageSync('token',res.data.data);
						uni.setStorageSync('phone',this.mobile);
						uni.switchTab({
								url: '/pages/index/index'
							});
					}else{
						uni.showToast({
							title:res.data.msg
						})
					}						
				},
				fail: (res) => {
					uni.showToast({
						title:'网络错误'
					})
				}
			})
		}
	},		
}
